#page-volumes {
  .row {
    + {
      .row {
        margin-top: 1rem;
      }
    }
  }
  .empty-box {
    background-color: var(--secondary-color);
    border-radius: 10px;
    height: 400px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .spinner {
    margin: 5rem 0;
    width: 100px;
    height: 100px;
  }
}
