.info-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    padding: 0 2.5vw;
    height: 10vh;
    font-family: inherit;
}

.tool-names {
    display: flex;
    cursor: pointer;
}

.tool-times {
    display: flex;
    background-color: #1E1F25;
    border-radius: 10px;
    cursor: pointer;
}

.tool-names>div {
    padding: 10px 30px;
    margin-right: 10px;
    transition: all 0.5s;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
}

.tool-names>div .circle {
    position: absolute;
    background-color: rgb(194, 186, 186);
    width: 100px;
    height: 100px;
    border-radius: 50%;
    transform: translate(-50%, -50%) scale(0);
    animation: scale 0.5s linear;
}

.tool-names>div:hover {
    background-color: #1E1F25;
}

.tool-names>div.active {
    background-color: #1E1F25;
}

.tool-times div {
    padding: 10px 15px;
    transition: all 0.3s;
}

.tool-times div:nth-child(1):hover {
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    background-color: #171719;
}

.tool-times div:nth-last-child(1):hover {
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: #171719;
}

.tool-times div:hover {
    background-color: #171719;
}


.tool-times div:nth-child(1).active {
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    background-color: #171719;
}

.tool-times div:nth-last-child(1).active {
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: #171719;
}

.tool-times div.active {
    background-color: #171719;
}


@keyframes scale {
    to {
        transform: translate(-50%, -50%) scale(3);
        opacity: 0;
    }
}



.home-trending-section,
.home-minting-section,
.home-ranking-section {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 15px;
}